@import url(https://fonts.googleapis.com/css2?family=Montserrat+Alternates:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap);
*{box-sizing:border-box}html body{font-family:"Montserrat Alternates", sans-serif}

.App{overflow:auto;-ms-scroll-snap-type:y mandatory;scroll-snap-type:y mandatory}

.Landing-One{min-height:100vh;scroll-snap-align:start;background:linear-gradient(90deg, #fff 75%, #408af8 25%);color:#20579e;padding-bottom:74px}.Landing-One header{height:60px;position:relative}.Landing-One header span.job-position{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);text-transform:uppercase;font-size:26px;font-weight:300}.Landing-One main{padding-left:6.5%;padding-right:6.5%;display:flex;justify-content:space-between;position:relative;height:580px}.Landing-One main .mini-description{width:56.1%;padding-top:60px;padding-left:45px}.Landing-One main .mini-description span.desc_name{font-size:40px;font-weight:500;line-height:1.3}.Landing-One main .mini-description p{margin-top:40px;line-height:2}.Landing-One main .mini-description .a-link-button{display:inline-block;margin-top:20px;text-decoration:none;background-color:transparent;border:2px solid #4e6fa5;border-radius:0.6em;color:#4e6fa5;font-size:14px;line-height:1;padding:1.2em 2.8em;text-align:center;text-transform:uppercase;font-weight:700;transition:box-shadow 300ms ease-in-out, color 300ms ease-in-out}.Landing-One main .mini-description .a-link-button:hover,.Landing-One main .mini-description .a-link-button:focus{color:#fff;outline:0;box-shadow:0 0 40px 40px #4e6fa5 inset}.Landing-One main .block-photo{background-color:#e2e2e2;width:29.89%;z-index:1}.Landing-One main .block-decoration{z-index:0;background:#fff;background-image:radial-gradient(#c3c6cd 2px, transparent 0);background-size:30px 30px;background-position:-5px 0px;position:absolute;width:11%;height:603.5px;left:930px;top:50px}

.Landing-Two{min-height:100vh;padding-bottom:30px}.Landing-Two header{position:relative;height:120px}.Landing-Two header span.span-header-name{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);font-size:16px;color:#4e6fa5;font-weight:700;line-height:1;text-transform:uppercase}.Landing-Two header p.p-header-text{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);font-size:16px;margin-top:30px}.Landing-Two main{width:75%;margin-left:auto;margin-right:auto;display:flex;flex-direction:column;position:relative}.Landing-Two main div.cards-portfolio{z-index:1;display:flex;justify-content:space-between;flex-wrap:wrap;height:440px}.Landing-Two main div.cards-portfolio div.card{width:350px;height:195px;background-color:#4089f8}.Landing-Two main .a-link-button{display:inline-block;margin-top:30px;text-decoration:none;background-color:transparent;border:2px solid #4e6fa5;border-radius:0.6em;color:#4e6fa5;font-size:14px;line-height:1;padding:1.2em 2.8em;text-align:center;text-transform:uppercase;font-weight:700;transition:box-shadow 300ms ease-in-out, color 300ms ease-in-out;align-self:center}.Landing-Two main .a-link-button:hover,.Landing-Two main .a-link-button:focus{color:#fff;outline:0;box-shadow:0 0 40px 40px #4e6fa5 inset}.Landing-Two main .block-decoration{z-index:0;background:#fff;background-image:radial-gradient(#c3c6cd 2px, transparent 0);background-size:30px 30px;background-position:-5px 0px;position:absolute;width:11%;height:200px;width:200px;left:-80px;top:-80px}

.Landing-Three{min-height:100vh;background:linear-gradient(135deg, #408af8 20%, #fff 33%)}.Landing-Three header{height:60px;position:relative}.Landing-Three header span.interests-name{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);text-transform:uppercase;font-size:16px;font-weight:700;color:#4e6fa5;line-height:1}.Landing-Three main{position:relative;width:80%;margin-left:auto;margin-right:auto}.Landing-Three main div.interests-cards{margin-top:20px;height:470px;display:flex;flex-direction:column;justify-content:space-between}.Landing-Three main div.interests-cards div.row-one{display:flex;justify-content:space-between}.Landing-Three main div.interests-cards div.row-two{display:flex;justify-content:space-between}.Landing-Three main div.interests-cards div.int-card{z-index:1;width:200px;height:200px;border-radius:50%}.Landing-Three main div.interests-cards div.int-card .img-wrapper{overflow:hidden;width:200px;height:200px;border-radius:50%}.Landing-Three main div.interests-cards div.int-card .img-wrapper .int-img{width:200px;height:200px}.Landing-Three main div.interests-cards div.int-card .img-name{text-align:center}.Landing-Three main .a-link-button{display:inline-block;margin-top:70px;text-decoration:none;background-color:transparent;border:2px solid #4e6fa5;border-radius:0.6em;color:#4e6fa5;font-size:14px;line-height:1;padding:1.2em 2.8em;text-align:center;text-transform:uppercase;font-weight:700;transition:box-shadow 300ms ease-in-out, color 300ms ease-in-out;align-self:center;position:absolute;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%)}.Landing-Three main .a-link-button:hover,.Landing-Three main .a-link-button:focus{color:#fff;outline:0;box-shadow:0 0 40px 40px #4e6fa5 inset}.Landing-Three main .block-decoration{z-index:0;background:#fff;background-image:radial-gradient(#c3c6cd 2px, transparent 0);background-size:30px 30px;background-position:-5px 0px;position:absolute;width:203.5px;height:203.5px;left:92%;top:55vh}

